	//class mj.manager.UiManager
	var UiManager=(function(){
		function UiManager(){
			this._currentScene=null;
			this.topLayer=new Sprite();
			this.dialogLayer=new Sprite();
			this.maskLayer=new Sprite();
			this.sceneLayer=new Sprite();
			this.maskSprite=new Sprite();
			this._sysSetting=SysSetting.create("","","","");
			if (UiManager.instance !=null){
				throw new SingletonError("ResourceManager 是单例模式");
			}
		}

		__class(UiManager,'mj.manager.UiManager');
		var __proto=UiManager.prototype;
		__proto.init=function(){
			Console.log("UiManager.init()");
			Laya.stage.addChild(this.sceneLayer);
			Dialog1.manager;
			Laya.stage.addChild(this.maskLayer);
			Laya.stage.addChild(this.dialogLayer);
			Laya.stage.addChild(this.topLayer);
			Laya.stage.on("resize",this,this.onResize);
			this.onResize(null);
			this.maskSprite.on("click",this,function(){
			});
			this.maskLayer.mouseThrough=true;
		}

		__proto.onResize=function(e){
			var width=Laya.stage.width;
			var height=Laya.stage.height;
			this.maskSprite.size(width,height);
			this.maskLayer.size(width,height);
			this.maskSprite.graphics.clear();
			this.maskSprite.graphics.drawRect(0,0,width,height,UIConfig.popupBgColor);
			this.maskSprite.alpha=UIConfig.popupBgAlpha;
		}

		__proto.showMask=function(){
			this.maskLayer.addChild(this.maskSprite);
			this.maskSprite.mouseEnabled=true;
			this.maskSprite.mouseThrough=false;
		}

		__proto.hideMask=function(){
			this.maskSprite.removeSelf();
		}

		__proto.goMain=function(){
			var main=new MainScene();
			if (UserManager.instance.user){
				main.update(UserManager.instance.user);
			}
			this.goScene(main);
			return main;
		}

		__proto.goScene=function(scene){
			Loading.instance.hide();
			if (this._currentScene){
				this._currentScene.destroy();
				this.sceneLayer.removeChild(this._currentScene.getDisplayObject());
			}
			Dialog1.closeAll();
			this._currentScene=scene;
			scene.init();
			this.sceneLayer.addChild(scene.getDisplayObject());
		}

		__proto.changeSysSetting=function(msg){
			if (msg==null){
				msg=SysSetting.create("","","","");
			}
			if (msg.payInfo==null){
				msg.payInfo=";"
			}
			if (msg.radio==null){
				msg.radio=";"
			}
			if (msg.agreement==null){
				msg.agreement=";"
			}
			if (msg.notice==null){
				msg.notice=";"
			}
			this._sysSetting=msg;
			if (this.currentScene && (this.currentScene instanceof mj.scene.MainScene )){
				(this.currentScene).changeRadio(msg.radio);
				(this.currentScene).changeNewNotice();
			}
		}

		__proto.changeRadio=function(radio){
			if (radio==null){
				radio="";
			}
			if (this._sysSetting){
				this._sysSetting.radio=radio;
			}
			if (this.currentScene && (this.currentScene instanceof mj.scene.MainScene )){
				(this.currentScene).changeRadio(radio);
			}
		}

		__getset(0,__proto,'sysSetting',function(){
			return this._sysSetting ? this._sysSetting :SysSetting.create("","","","");
		});

		__getset(0,__proto,'currentScene',function(){
			return this._currentScene;
		});

		__static(UiManager,
		['instance',function(){return this.instance=new UiManager();}
		]);
		return UiManager;
	})()


